/* =============================================
reset
============================================= */

html {background-color:#0f0f12;}
html::-webkit-scrollbar, div::-webkit-scrollbar {display:none;}
p, a, button, span, label, h1, h2, h3, h4, h5, h6 {margin:0; font-family:'Pretendard'; color:#fff;}
p, a, button, span {font-size:14px;}
h1 {font-size:36px; font-weight:900;}
h2 {font-size:32px; font-weight:800;}
h3 {font-size:24px; font-weight:700;}
h4 {font-size:20px; font-weight:600;}
h5 {font-size:16px; font-weight:400;}
h6 {font-size:13px; font-weight:300;}
.fw100 {font-weight:100;}
.fw200 {font-weight:200;}
.fw300 {font-weight:300;}
.fw400 {font-weight:400;}
.fw500 {font-weight:500;}
.fw600 {font-weight:600;}
.fw700 {font-weight:700;}
.fw800 {font-weight:800;}
.fw900 {font-weight:900;}
.mt5 {margin-top:5px;}
.mt10 {margin-top:10px;}
.mt15 {margin-top:15px;}
.mt20 {margin-top:20px;}
.mt25 {margin-top:25px;}
.mt30 {margin-top:30px;}
.mt35 {margin-top:35px;}
.mt40 {margin-top:40px;}
.mt45 {margin-top:45px;}
.mt50 {margin-top:50px;}
.mt55 {margin-top:55px;}
.mt60 {margin-top:60px;}
a, a:link, a:visited, a:active, a:hover {text-decoration:none; color:#222; cursor:pointer;}
a, input, button {-webkit-tap-highlight-color:transparent;}
ul {list-style:none; margin-block-start:0; margin-block-end:0; margin-inline-start:0px; margin-inline-end:0px; padding-inline-start:0;}
button {padding:0; margin:0; background:none; border:none; cursor:pointer;}
.btn-filter {height:36px; padding:0 16px; border:none; border-radius:6px; background-color:rgb(64, 62, 68);}
.btn-filter .arrow {width:12px; height:12px; margin-left:8px; background:url('../src/assets/btn-arrow.svg') center no-repeat; background-size:12px;}
button:hover {background-color:none;}
input:focus {outline:none;}
div, header, input {box-sizing:border-box;}
@keyframes fadein {0% {opacity:0;}; 100% { opacity:100%;}}

