/* =============================================
App
============================================= */

.contents {margin:80px 0; padding:0 20px; background-color:#0f0f12; animation:fadein .1s;}
.contents ul li.section:nth-child(n+2) {margin-top:30px;}

/* =============================================
Header
============================================= */

.App-header {display:flex; align-items:center; justify-content:space-between; position:fixed; top:0; width:100%; height:60px; padding:0 20px; background-color:#0f0f12; background-color:rgba(15, 15, 18, 0.5); transition:.2s; backdrop-filter:blur(5px); -webkit-backdrop-filter:blur(5px);}
.logo div {width:80px; height:40px; background:url(../src/assets/logo.svg) center no-repeat; background-size:80px;}
.nav {display:flex;}
.slide-menu {width:200px; height:200vh; background-color:#2e5bff; position:absolute; right:-200px; transition:.3s;}

/* =============================================
home
============================================= */

.nav-con-bottom {width:100%; height:60px; background-color:#18161c; border-top:1px solid #333; border-radius:30px 30px 0 0; position:fixed; bottom:0; display:flex; justify-content:space-between; align-items:center; z-index:100;}
.nav-con-bottom a {width:50%; height:60px; display:flex; flex-flow:column; justify-content:center; align-items:center;}
.nav-con-bottom a .btn-tab {display:none; width:20px; height:20px; color:black;}
.nav-con-bottom .link-tab .btn-tab.on, .nav-con-bottom .link-tab.on .btn-tab {display:none;}
.nav-con-bottom .link-tab .btn-tab, .nav-con-bottom .link-tab.on .btn-tab.on {display:block;}
.nav-con-bottom a p {font-size:11px; color:#777; margin-top:6px; text-align:center; position:relative;}
.nav-con-bottom a.on p {color:#fff; font-weight:700;}
.main-con ul .main-box01 {display:flex; flex-wrap:wrap; background-color:#0f0f12; padding:20px; margin:-20px -20px 0;}
.main-con ul .main-box01 a:nth-child(2n) {margin-left:10px;}
.main-con ul .main-box01 a:nth-child(n+3) {margin-top:10px;}
.main-con ul .main-box01 a {position:relative; width:calc((100% - 10px) * .5); height:calc((100vw - 50px) * .4); padding:20px; background-color:#18161c; border-radius:10px; box-sizing:border-box;}
.main-con ul .main-box01 a .banner .txt h3 {color:#fff;}
.main-con ul .main-box01 a .banner .simb-area {position:absolute; right:20px; bottom:20px; width:50px; height:50px; border-radius:30%; background-color:#fff; opacity:.1;}
.main-con ul .swiper-box01 .mySwiper {margin-left:-20px; margin-right:-20px; padding-left:20px; padding-right:20px;}
.main-con ul .swiper-box01 .mySwiper .slide {width:100%; aspect-ratio:1.2 / 1; border-radius:10px; margin-top:10px; height:auto; background-size:cover;}
.main-con ul .swiper-box01 .mySwiper .slide.slide01 {background:url(../src/assets/img-swiper_01.jpg) center no-repeat; background-size:cover;}
.main-con ul .swiper-box01 .mySwiper .slide.slide02 {background:url(../src/assets/img-swiper_02.jpg) center no-repeat; background-size:cover;}
.main-con ul .swiper-box01 .mySwiper .slide.slide03 {background:url(../src/assets/img-swiper_03.jpg) center no-repeat; background-size:cover;}
.main-con ul .swiper-box01 .mySwiper .slide.slide04 {background:url(../src/assets/img-swiper_04.jpg) center no-repeat; background-size:cover;}
.main-con ul .swiper-box01 .mySwiper .slide.slide05 {background:url(../src/assets/img-swiper_05.jpg) center no-repeat; background-size:cover;}
.main-con ul .swiper-box01 .mySwiper .slide.slide06 {background:url(../src/assets/img-swiper_06.jpg) center no-repeat; background-size:cover;}

/* =============================================
apart
============================================= */

.Apart .apart-con ul .section.list-box .filter-area {display:flex; position:fixed; top:0; width:100%; height:120px; padding:72px 0 12px; margin:0 -20px; background-color:rgba(15, 15, 18, 0.5); transition:.2s; backdrop-filter:blur(5px); -webkit-backdrop-filter:blur(5px);}
.Apart .apart-con ul .section.list-box .filter-area .btn-filter {display:flex; align-items:center;}
.Apart .apart-con ul .section.list-box .filter-area .btn-filter ~ .btn-filter {margin-left:10px;}
.Apart .apart-con ul .section.list-box .filter-area .btn-filter > span {font-size:15px;}
.Apart .apart-con ul .section.list-box .filter-area .slide {width:auto;}
.Apart .apart-con ul .section.list-box .filter-area .slide:first-of-type {margin-left:20px;}
.Apart .apart-con ul .section.list-box .list-area {margin-top:130px;}
.Apart .apart-con ul .section.list-box .list-area .list-group a {display:flex;}
.Apart .apart-con ul .section.list-box .list-area .list-group a .img-thumb {width:calc(38%); height:calc((100vw - 40px) * .38 * .8); background-color:#fff; border-radius:6%; text-align:center; overflow:hidden;}
.Apart .apart-con ul .section.list-box .list-area .list-group a .img-thumb img {display:inline; width:auto; height:100%;}
.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area {width:calc(58%); margin-left:4%;}
.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area .txt-group {display:flex; flex-flow:column; justify-content:space-between; height:calc((100vw - 40px) * .38 * .8);}
.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area .txt-group .bdg-group span{color:#999;}
.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area .txt-group .bdg-group .room3::after {content:'쓰리룸';}
.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area .txt-group .bdg-group .room2::after {content:'투룸';}
.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area .txt-group .bdg-group .room1::after {content:'원룸';}
.Apart .apart-con ul .section.list-box .list-area .list-group ~ .list-group {border-top:1px solid #333; padding-top:20px; margin-top:20px;}

/* =============================================
mypage
============================================= */

.MyPage .set-con {width:100%; margin:0 -20px;}
.MyPage .set-con ul li {width:calc(100% + 40px); padding:20px 20px; box-sizing:border-box;}
.MyPage .set-con ul li + li {border-top:1px solid #eee;}

/* =============================================
popup bottom
============================================= */

.pop-bottom {display:none; position:absolute; top:0; left:0; width:100%; height:200vh; z-index:500;}
.pop-bottom .pop-bottom-area {position:fixed; bottom:0; width:100%; max-height:400px; background-color:#18161c; border-top:1px solid #333; border-radius:30px 30px 0 0; overflow:hidden;}
.pop-bottom .pop-bottom-area .tit-area {display:flex; align-items:center; justify-content:space-between; width:100%; height:70px; padding:0 20px;}
.pop-bottom .pop-bottom-area .tit-area .btn-close div {width:16px; height:16px; background:url('../src/assets/btn-close.svg') center no-repeat; background-size:16px;}
.pop-bottom .pop-bottom-area .contents-area {width:100%; padding:0 20px 40px; max-height:360px; overflow:scroll;}
.pop-bottom .pop-bottom-area .contents-area .data {list-style:none;}
.pop-bottom .pop-bottom-area .contents-area .data.on {padding:0 20px; margin:0 -20px; background-color:#2e5bff;}
.pop-bottom .pop-bottom-area .contents-area .data ~ .data {border-top:1px solid #333;}
.pop-bottom .pop-bottom-area .contents-area .data button {width:100%; text-align:left;}
.pop-bottom .pop-bottom-area .contents-area .data button > h5 {padding:20px 0;}
.pop-bottom .dimmed {position:absolute; width:100%; height:200vh; background-color:#000; opacity:.5;}
.pop-bottom .pop-bottom-area .contents-area .inp-chk-area label {display:flex; align-items:center; justify-content:space-between; width:100%; padding:15px 0; font-size:16px;}
.pop-bottom .pop-bottom-area .contents-area .inp-chk-area label ~ label {border-top:1px solid #333;}
.pop-bottom .pop-bottom-area .contents-area .inp-chk-area label .chkbox {width:24px; height:24px; background:url('../src/assets/check-before.svg') center no-repeat; background-size:24px; transition:.1s;}
.pop-bottom .pop-bottom-area .contents-area .inp-chk-area label .chkbox-checked {position:absolute; right:20px; width:24px; height:24px; background:url('../src/assets/check-checked.svg') center no-repeat; background-size:24px; transition:.2s; opacity:0;}
.pop-bottom .pop-bottom-area .contents-area .inp-chk-area .inp-chk {display:none;}
.pop-bottom .pop-bottom-area .contents-area .inp-chk-area .inp-chk:checked + label .chkbox-checked {width:24px; height:24px; background:url('../src/assets/check-checked.svg') center no-repeat; background-size:24px; transition:.2s; opacity:1;}
.pop-bottom.FiltType .pop-bottom-area .contents-area {padding-bottom:20px;}
.pop-bottom .pop-bottom-area .btn-area {display:flex; height:60px;}
.pop-bottom .pop-bottom-area .btn-area .btn-primary {flex:1; background-color: #2e5bff; font-size:16px;}

/* =============================================
detail
============================================= */

.Detail .detail-con {margin-top:-20px;}
.Detail .detail-con .btn-back {position:absolute; top:22px; right:20px; z-index:999; transition:.2s;}

/* =============================================
homephoto
============================================= */

.Homephoto {position:relative; width:100vw; margin:0 -20px;}
.Homephoto .homephoto-con .photo-area {position:fixed; top:0; z-index:-1;}
.Homephoto .homephoto-con .photo-area img {width:100vw; height:calc(100vw * .8); margin:0 -20px; object-fit:cover; image-rendering:-webkit-optimize-contrast; transform:translateZ(0); backface-visibility:hidden; transition:.2s;}
.Homephoto .homephoto-con .info-area {padding:20px 0; margin-top:calc(100vw * .8);}